<template>
  <view class="add-daily-report-container">
    <view class="bgBox"></view>
    <view style="z-index: 1; position: relative">
      <view class="topHeader"> 添加报销单 </view>
      <u-form :model="form" ref="uForm" label-position="top" label-width="100%">
        <u-form-item label=" " prop="workContent">
          <u-textarea
            v-model="form.workContent"
            type="textarea"
            placeholder="请输入报销内容"
            height="300"
            maxlength="10000"
            @change="handleChange"
          />
        </u-form-item>
        <u-form-item label="月份" prop="workPhotos">
          <u-input v-model="form.month" placeholder="请输入月份" clearable />
        </u-form-item>

        <u-form-item label="报销人" prop="workPhotos">
          <u-input v-model="form.caozuo" placeholder="请输入" clearable />
        </u-form-item>
        <u-form-item label="备注" prop="workPhotos">
          <u-input v-model="form.remark" placeholder="请输入" clearable />
        </u-form-item>
      </u-form>
      <view
        style="display: flex; justify-content: space-between; margin-top: 30px"
      >
        <u-button style="width: 30%" @click="handleSubmit" type="primary"
          >生成</u-button
        >
        <u-button
          style="width: 30%"
          v-if="showExportBtn"
          @click="exportToExcel(1)"
          type="success"
          >预览</u-button
        >
        <u-button
          style="width: 30%"
          v-if="showExportBtn"
          @click="exportToExcel(2)"
          type="warning"
          >分享</u-button
        >
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      form: {
        workContent: "",
        month: new Date().getMonth(),
        zixun: "",
        caozuo: "",
        remark: "货拉拉",
      },
      userInfo: null,
      showExportBtn: "",
      table: {
        data: [],
      },
    };
  },
  onLoad() {},
  methods: {
    calcNum() {
      return this.table.data.reduce((accumulator, currentObject) => {
        return (
          parseFloat(accumulator || 0) + parseFloat(currentObject.money || 0)
        );
      }, 0);
    },

    handleChange() {
      // 输入框内容变化时触发
      this.showExportBtn = "";
    },
    checkTodayDailyReport() {},
    async handleSubmit() {
      if (!this.form.workContent) {
        uni.showToast({
          title: "请输入工作内容",
          icon: "none",
        });
        return;
      }

      let str = this.form.workContent

        .replace(/<p>/g, "")
        .replace(/&nbsp;/g, "")
        .split("\n");

      // console.log(this.form.value);

      let arr = str.filter(el => el);

      this.filterTableData(arr);
    },

    filterTableData(arr) {
      let tablist = [];
      arr.forEach((el, index) => {
        var regex = /\d+\.?\d*/g;
        let as = el.match(regex);

        if (!as) return;

        if (as.length < 2) {
          let at = arr[index - 1].match(regex);
          as.unshift(at[0]);
        } else if (as.length > 2) {
          var sum = as.reduce(function (up, cur, index) {
            return index === 0 ? up : parseFloat(up) + parseFloat(cur);
          }, 0);
          as = [as[0], String(sum)];
        }

        if (!this.form.month) {
          this.form.month;
          this.$set(this.form, "month", as[0].split(".")[0]);
        }

        tablist.push({
          date: as[0],
          msg: el.replace(as[0], "").replace(as[1], ""),
          money: Number(as[1]).toFixed(2),
          type: this.form.remark || "货拉拉",
          name: this.form.caozuo || "贠清",
        });
      });

      tablist.sort(function (a, b) {
        return a.date.split(".")[1] - b.date.split(".")[1];
      });

      this.filterData(tablist);
    },

    async filterData(arr) {
      this.table.data = arr;

      await this.createExcel();
    },

    // 导出为Excel
    async createExcel() {
      const headers = [];
      const rows = this.table.data.map(item => [
        this.date || "",
        item.msg || "",
        item.money || "",
        item.type || "",
        item.name || "",
      ]);

      uni.showLoading({
        title: "正在生成文件...",
      });

      try {
        const res = await uniCloud.callFunction({
          name: "exportReports",
          data: {
            headers,
            rows,
            table: {
              ...this.form,
              allMoney: this.calcNum(),
            },
            fileName: `报销单_${new Date().toLocaleDateString()}.xlsx`,
          },
        });
        this.showExportBtn = res.result.data.tempUrl;
      } catch (error) {
        uni.hideLoading();
        uni.showToast({
          title: "导出失败: " + error.message,
          icon: "none",
        });
      }
      // 添加导出按钮显示
      // this.showExportBtn = true;
      uni.hideLoading();
    },

    splitStringByNumbers(str) {
      // 使用正则表达式匹配所有的数字
      const matches = str.match(/\d+/g) || [];
      // 将匹配到的字符串数字转换为数字类型
      return matches.map(Number);
    },
    splitChineseWords(str) {
      return str.match(/[a-z|A-Z]+/g) || str.match(/[\u4e00-\u9fa5]+/g);
    },

    exportToExcel(type) {
      if (type == 1) {
        // 预览
        uni.downloadFile({
          url: this.showExportBtn, // 下载url
          success(res) {
            console.log("ressss", res);
            if (res.statusCode == 200) {
              console.log("res.statusCode", res);
              uni.openDocument({
                filePath: res.tempFilePath,
                fileType: "xlsx",
                success: function (ress) {
                  console.log("打开文档成功", ress);
                },
                fail: function (err) {
                  console.log("保存失败：", err);
                },
              });
            }
          },
          fileName: "excelname",
          fail: console.error,
        });
      } else {
        let that = this;
        uni.downloadFile({
          url: this.showExportBtn, // 下载url
          success(res) {
            console.log("ressss", res);
            if (res.statusCode == 200) {
              console.log("res.statusCode", res);

              // #ifdef MP-WEIXIN
              wx.shareFileMessage({
                filePath: res.tempFilePath,
                success(data) {
                  console.log("转发成功！！！", data);
                },
                fileName: that.form.month + "月份报销单.xlsx",
                fail: console.error,
              });
              // #endif
            }
          },
          fileName: "excelname",
          fail: console.error,
        });
      }
    },
  },
};
</script>

